@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  @variants responsive {
    .text-shadow-blue {
      text-shadow: 0 0 20px #355dff;
    }
    .box-shadow-blue {
      box-shadow: 0 0 20px #355dff;
    }
    .button-box-shadow-hover {
      box-shadow: 0 0 40px #355dff;
    }
    .main-gradient-actual {
      background-color: #355dff;
      background-image: linear-gradient(
        320deg,
        rgba(108, 94, 207, 1) 40%,
        rgba(53, 93, 255, 1) 75%
      );
      background-repeat: no-repeat;
      background-size: 125%;
    }
    .main-gradient {
      background-color: #355dff;
      background-image: url(turbo.png),
        linear-gradient(
          320deg,
          rgba(108, 94, 207, 1) 40%,
          rgba(53, 93, 255, 1) 75%
        );
      background-repeat: no-repeat;
      background-size: 125%;
    }
    .logo-bg {
      background: linear-gradient(
        320deg,
        rgba(108, 94, 207, 1) 40%,
        rgba(53, 93, 255, 1) 75%
      );
    }
    .logo-bg2 {
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    .error {
      background-color: #f8d7da;
      border-color: #f5c2c7;
      color: #842029;
    }
    .message {
      box-shadow: 0 0 20px #355dff;
      background-color: #f8d7da;
      border-color: #f5c2c7;
      color: #842029;
    }
    .approve {
      background-color: #0be881;
      box-shadow: #0be881 0px 0px 20px;
    }
    .remove {
      background-color: #eb3b5a;
      box-shadow: #eb3b5a 0px 0px 20px;
    }
  }
}

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap");
* {
  outline: none;
  box-sizing: border-box;
}
html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}
img {
  max-width: 100%;
}
:root {
  --body-font: "Inter", sans-serif;
  --theme-bg: #1f1d2b;
  --body-color: #808191;
  --main-blue: #355dff;
  --button-bg: #353340;
  --border-color: rgba(128, 129, 145, 0.24);
  --video-bg: #252936;
  --delay: 0s;
  --hover-color: #355dff;
}

body {
  font-family: var(--body-font);
  color: var(--body-color);
  background-image: url(bg.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-blend-mode: multiply;
  background-color: rgba(18, 21, 39, 0.86);
  animation-name: color;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1em 2em;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

a {
  text-decoration: none;
}

@keyframes color {
  0% {
    background-color: rgba(18, 21, 39, 0.3);
  }
  25% {
    background-color: rgba(18, 21, 39, 0.6);
  }
  50% {
    background-color: rgba(18, 21, 39, 0.9);
  }
  75% {
    background-color: rgba(18, 21, 39, 0.6);
  }
  100% {
    background-color: rgba(18, 21, 39, 0.3);
  }
}

body:before {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    163deg,
    #1f1d2b 21%,
    rgba(31, 29, 43, 0.3113620448) 64%
  );
  opacity: 0.4;
  content: "";
}

.sidebar-link:hover,
.sidebar-link.is-active {
  color: #fff;
}

.sidebar-link:hover:nth-child(2n + 1) svg,
.is-active:nth-child(2n + 1) svg {
  background: var(--hover-color);
  border-radius: 24px;
}

.is-active svg {
  background: linear-gradient(
    320deg,
    rgba(108, 94, 207, 1) 40%,
    rgba(53, 93, 255, 1) 75%
  ) !important;
}

::-webkit-scrollbar {
  width: 6px;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background-color: rgba(21, 20, 26, 0.63);
  border-radius: 10px;
}
.anim {
  animation: bottom 0.8s var(--delay) both;
}
.seperate {
  width: 3px;
  height: 3px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 50%;
  background-color: #fff;
  margin: 0 6px;
}
@keyframes bottom {
  0% {
    transform: translateY(100px);
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
